* { box-sizing: border-box; outline: none;}
html {
    min-height: 100%;
}
body {
    min-height: 100%;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: antialiased;
    font-family: "Helvetica Neue",sans-serif;
    text-align: left;
    background-color: #1d1f2c;
}


/* NAVIGATION */
nav {
    position: fixed;
    z-index: 2;
    top: 0;
    left: 0;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 15px 20px;
    will-change: transform, background-color;
    transition: transform .2s cubic-bezier(0.46, 0.03, 0.52, 0.96), background-color .2s cubic-bezier(0.46, 0.03, 0.52, 0.96);
}
.nav_logo {
    display: flex;
    align-items: center;
    width: 134px;
}
.nav_links {
    position: relative;
    display: flex;
    align-items: center;
}
.nav_inputholder {
    position: relative;
    display: flex;
    align-items: center;
    margin-left: 10px;
    width: 234px;
    height: 33px;
}
.nav_inputholder input {
    height: 100%;
    width: 100%;
    color: rgba(255,255,255,0.8);
    /*font-family: "Helvetica Neue";*/
    font-size: 14px;
    padding: 0 10px;
    padding-right: 36px;
    font-weight: 400;
    background-color: rgba(20,21,31,0);
    transition: background-color .2s cubic-bezier(0.46, 0.03, 0.52, 0.96), border-color .2s cubic-bezier(0.46, 0.03, 0.52, 0.96);
    border: 2px solid rgba(73,75,87,.5);
}
.nav_inputholder input:hover {
    border-color: rgba(73,75,87,.6);
}
.nav_inputholder input:focus {
    background-color: rgba(20,21,31,0.5);
}
.nav_inputholder img {
    width: 16px;
    position: absolute;
    right: 12px;
    top: 50%;
    -webkit-transform: translate(0,-50%);
    -ms-transform: translate(0,-50%);
    transform: translate(0,-50%);
    z-index: -1;
}
.nav_link {
    cursor: pointer;
    margin-left: 25px;
    display: flex;
    align-items: center;
    will-change:opacity;
    transition: opacity .2s cubic-bezier(0.46, 0.03, 0.52, 0.96);
}
.nav_link:hover {
    opacity: 0.9;
}
.user.nav_link {
    display: none;
}
.user.nav_link.show {
    display: flex;
}
.sign_in.nav_link {
    display: none;
}
.sign_in.nav_link.show {
    display: flex;
}
.user.nav_link:hover {
    opacity: 1;
}
.link_image {
    display: flex;
    align-items: center;
    width: 16px;
    margin-right: 7px;
}
.user .link_image {
    width: 22px;
}
.link_name {
    color: white;
    font-size: 15px;
    font-weight: 500;
}
.link_arrow {
    display: flex;
    position: relative;
    align-items: center;
    width: 17px;
    margin-left: 6px;
}
.nav_dropdown {
    position: absolute;
    top: 42px;
    right: 0;
    box-shadow: 0 5px 42px 1px rgba(29, 31, 44, 0.22);
    border: 2px solid #2a2c3b;
    will-change: opacity, transform;
    transform: rotateX(90deg);
    transform-origin: top;
    opacity: 0;
    transition: opacity .2s cubic-bezier(0.46, 0.03, 0.52, 0.96), transform .2s cubic-bezier(0.46, 0.03, 0.52, 0.96);
}
.dropdown_link {
    padding: 18px;
    color: rgba(255,255,255,.8);
    /*font-family: "Helvetica Neue";*/
    font-size: 14px;
    font-weight: 500;
    background-color: rgba(34, 36, 49, 0.95);
    display: block;
    border-bottom: 2px solid #2a2c3b;
    cursor: pointer;
}
.dropdown_link:last-child {
    border-bottom: none;
}
.nav_dropdown.show {
    transform: rotateX(0deg);
    opacity: 1;
}
/* NAV Ending*/


/* HEADER */
main {
    position: relative;
    padding: 80px 0px 25px;    
}
/*GRADIENT*/

.gradient {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 397px;
    z-index: -1;
    background: linear-gradient(0deg, #1d1f2c, rgba(29,31,44,0)), linear-gradient(90deg, #362929, #212433);
}
.background {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 0.5;
    background: url(../../assets/v1/pattern.png) repeat-x;
    background-size: 369px 397px;
}
header {
    max-width: 920px;
    margin: auto;
    display: flex;
}
.video {
    max-width: 607px;
    display: flex;
    align-items: center;
    flex-grow: 2;
}
.video_info {
    max-width: 234px;
    display: flex;
    flex-flow: wrap;
    margin-left: 20px;
}
.video_properties {
    align-self: flex-start;
    margin-bottom: 30px;
}
.video_user {
    display: flex;
    align-items: center;
}
.user_icon {
    display: flex;
    align-items: center;
    width: 60px;
    margin-right: 20px;
}
.user_name {
    color: #fffeff;
    font-size: 22px;
    font-weight: 400;
    will-change:opacity;
    transition: opacity .2s cubic-bezier(0.46, 0.03, 0.52, 0.96);
}
.user_name:hover {
    opacity: 0.9;
}
.video_type {
    display: flex;
    margin: 22px 0 9px;
    color: #a9aab2;
    font-size: 17px;
    font-weight: 400;
    will-change:opacity;
    transition: opacity .2s cubic-bezier(0.46, 0.03, 0.52, 0.96);
}
.video_type:hover {
    opacity: 0.9;
}
.video_date {
    color: #4a4c5e;
    font-size: 15px;
    font-weight: 400;
}
.vr_type {
    margin-top: 17px;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 47px;
    height: 25px;
    color: white;
    font-size: 15px;
    font-weight: 500;
    background-color: rgba(91,125,210,0.75);
}
.vr_type.two {
    position: absolute;
    top: 0;
    left: 0;
    margin: 0;
    cursor: inherit;
    transform-origin: top left;
    transform: scale(1.5);
}
.video_feedback {
    align-self: flex-end;
}
.video_share {
    display: flex;
    align-items: baseline;
    padding-bottom: 13px;
}
.shares_title {
    color: #50525f;
    font-size: 15px;
    font-weight: 400;
    padding-bottom: 5px;
}
.shares_stats {
    display: flex;
    align-items: center;
}
.shares_number {
    color: #fffeff;
    font-family: "Avant Garde I T Cby B T";
    font-size: 29px;
    font-weight: 300;
    margin-right: 42px;
}
.shareto_title {
    color: #50525f;
    font-size: 15px;
    font-weight: 400;
    padding-bottom: 14px;
}
.shares_options {
    display: flex;
    align-items: center;
}
.shares_option {
    display: flex;
    align-items: center;
    height: 18px;
    margin-right: 15px;
    will-change:opacity;
    transition: opacity .2s cubic-bezier(0.46, 0.03, 0.52, 0.96);
}
.shares_option:hover {
    opacity: 0.9;
}
.shares_option img {
    height: 100%;
    width: initial;
}
.video_response {
    display: flex;
    align-items: center;
}
.video_likes {
    margin-right: 35px;
}
.likes_title {
    color: #50525f;
    font-size: 15px;
    font-weight: 400;
    margin-bottom: 5px;
}
.likes_stats {
    display: flex;
    align-items: center;
}
.likes_icon {
    position: relative;
    display: flex;
    align-items: center;
    width: 23px;
    margin-right: 10px;
}
.liked {
    opacity: 0;
    will-change:opacity;
    transition: opacity .2s cubic-bezier(0.46, 0.03, 0.52, 0.96);
}
.not_liked {
    position: absolute;
    top: 0;
    left: 0;
    opacity: 1;
    will-change: opacity;
    transition: opacity .2s cubic-bezier(0.46, 0.03, 0.52, 0.96);
}
.likes_icon:focus .liked {
    opacity:1;
}
.likes_icon:focus .not_liked {
    opacity: 0;
}
.likes_number {
    color: #fffeff;
    font-family: "Avant Garde I T Cby B T";
    font-size: 29px;
    font-weight: 300;
}
.views_title {
    color: #50525f;
    font-size: 15px;
    font-weight: 400;
    margin-bottom: 5px;
}
.views_number {
    color: #fffeff;
    font-family: "Avant Garde I T Cby B T";
    font-size: 29px;
    font-weight: 300;
}
/* HEADER ENDING */


/* RELATED */
.related {
    max-width: 920px;
    margin: auto;
}
.related_title {
    padding: 45px 0 25px;
    color: #eaeced;
    font-size: 28px;
    font-weight: 400;
}
.related_cards {
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
}
.carousel_title {
    max-width: 960px;
    padding: 0 20px;
    margin: 0 auto 25px;
    color: #eaeced;
    /*font-family: "Helvetica Neue";*/
    font-size: 22px;
    font-weight: 400;
}
.card {
    width: 235px;
    max-width: 235px;
    margin: 0 10px 40px;
}
.card_big {
    width: calc(100% - 20px);
    max-width: 920px;
    margin: 0 auto 40px;
    opacity: 0.5;
    will-change: opacity;
    transition: opacity .2s ease-in-out;
}
.variable-width-big .card_big {
    margin: 0 10px 40px;
}
.card_image {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
}
/* PLAY BUTTON*/
.card_play {
    position: absolute;
    width: 100px;
    height: 100px;
    top: 50%;
    left: 50%;
    border-radius: 50%;
    transform: translate(-50%,-50%);
    opacity: 0;
    will-change: opacity;
    transition: opacity .2s cubic-bezier(0.46, 0.03, 0.52, 0.96) , transform .2s cubic-bezier(0.46, 0.03, 0.52, 0.96);
}

.play_circle {
    position: absolute;
    border-radius: 50%;
    background-color: #ef7125;
    will-change: transform;
    transition: transform .2s cubic-bezier(0.46, 0.03, 0.52, 0.96);
}
.play_circle.one {
    height: 100px;
    width: 100px;
    top: 50%;
    left: 50%;
    transform-origin: top left;
    transform: scale(1.2) translate(-50%,-50%);
    opacity: 0.2;
}
.play_circle.two {
    top: 50%;
    left: 50%;
    transform: scale(0.8) translate(-50%,-50%);
    transform-origin: top left;
    height: 75px;
    width: 75px;
}
.play_triangle {
    position: absolute;
    top: 50%;
    left: 50%;
    transform-origin: top left;
    transform: translate(-50%,-50%);
    width: 0px;
    height: 0px;
    margin-left: 3px;
    border-top: 15px solid transparent;
    border-bottom: 15px solid transparent;
    border-left: 25px solid white;
}
.card_image:hover .card_play {
    opacity: 1;
}
.card_image:hover .play_circle {
    transform: scale(1) translate(-50%,-50%);
}
/* Play Ending*/
.card_properties {
    background-color: #222431;
    padding: 20px;
}
/* BIG CARD PROPERTIES */
.card_big .card_properties {
    padding: 25px 20px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.card_profile {
    display: flex;
    align-items: center;
}
.card_profile .card_type {
    margin-top: 0;
    margin-left: 20px;
    color: #9c9da7;
    font-size: 16px;
}
.card_info {
    display: flex;
    align-items: center;
}
.card_socials {
    display: flex;
    align-items: center;
}
.card_social {
    width: initial;
    height: 16px;
    margin-left: 15px;
}
.card_statistics {
    display: flex;
    align-items: center;
}
.card_likes, .card_views {
    display: flex;
    align-items: center;
    margin-left: 33px;
    color: #fffeff;
    font-family: "Avant Garde I T Cby B T";
    font-size: 20px;
    font-weight: 300;
}
.card_likes img {
    border-radius: 4px;
    border: 2px solid rgba(72, 75, 90, 0.5);
    background-color: rgba(34, 36, 49, 0.5);
    padding: 8px 6.5px;
    width: 34px;
    height: 34px;
    margin-right: 10px;
}
.card_views img {
    width: 20px;
    margin-right: 10px;
}
/* BIG CARD PROPERTIES END */
.card_name {
    display: flex;
    align-items: center;
}
.name_icon {
    display: flex;
    align-items: center;
    width: 30px;
    margin-right: 15px;
}
.name_context {
    color: #fffeff;
    font-size: 18px;
    font-weight: 400;
    will-change:opacity;
    transition: opacity .2s cubic-bezier(0.46, 0.03, 0.52, 0.96);
}
.name_context:hover {
    opacity: 0.9;
}
.card_type {
    display: flex;
    margin-top: 20px;
    color: #75778c;
    font-size: 17px;
    line-height: 20px;
    height: 40px;
    overflow: hidden;

    font-weight: 400;
    will-change:opacity;
    transition: opacity .2s cubic-bezier(0.46, 0.03, 0.52, 0.96);
}
.card_type:hover {
    opacity: 0.9;
}
.card_date {
    color: #4a4c5e;
    font-size: 15px;
    font-weight: 400;
}
.card_stats {
    padding: 0 20px 20px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: #222431;
}
.stats_views {
    color: #75778c;
    font-size: 15px;
    font-weight: 400;
    margin-right: 15px;
}
.stats_options {
    position: relative;
}
.stats_options_btn {
    cursor: pointer;
    display: flex;
    align-items: center;
    width: 24px;
    padding: 2px;
    opacity: 1;
    will-change: opacity, transform;
    transition: opacity .1s ease-in-out .1s, transform .1s ease-in-out .1s;
}
.stats_options_flag {
    cursor: pointer;
    display: none;
    align-items: center;
    width: 17px;
}
.stats_options.flag .stats_options_btn {
    display: none;
}
.stats_options.flag .stats_options_flag {
    display: flex;
}
.stats_options_delete {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 114px;
    height: 32px;
    position: absolute;
    top: 25px;
    left: 50%;
    color: rgba(255,255,255,.6);
    /*font-family: "Helvetica Neue";*/
    font-size: 12px;
    font-weight: 500;
    background-color: #323546;
    box-shadow: 0 5px 42px 1px rgba(29, 31, 44, 0.22);
    border-radius: 2px;
    cursor: pointer;
    -webkit-transform: translate(-50%, 0) scale(0.8);
    -ms-transform: translate(-50%, 0) scale(0.8);
    transform: translate(-50%, 0) scale(0.8);
    opacity: 0;
    z-index: -1;
    will-change: opacity, transform;
    transition: opacity .1s ease-in-out .1s, transform .1s ease-in-out .1s, z-index 0s ease .2s;
}
.stats_options_delete:before {
    content: '';
    position: absolute;
    top: -5px;
    left: 50%;
    -webkit-transform: translate(-50%, 0);
    -ms-transform: translate(-50%, 0);
    transform: translate(-50%, 0);
    border-bottom: 5px solid #323546;
    border-left: 4px solid transparent;
    border-right: 4px solid transparent;
}
.stats_options.show .stats_options_delete {
    opacity: 1;
    -webkit-transform: translate(-50%, 0) scale(1);
    -ms-transform: translate(-50%, 0) scale(1);
    transform: translate(-50%, 0) scale(1);
    z-index: 0;
    transition: opacity .1s ease-in-out .1s, transform .1s ease-in-out .1s, z-index 0s ease;
}
/* Stats Ending*/


