/* GAME DROPDOWN */
.types_game {
    position: relative;
    margin-right: 10px;
    width: 150px;
}
.game_button.top {
    color: rgba(255,255,255,.2);
    background-color: rgba(20,21,31,0);
    border: 1px solid rgba(73,75,87,.5);
    transition: color .2s cubic-bezier(0.46, 0.03, 0.52, 0.96), background-color .2s cubic-bezier(0.46, 0.03, 0.52, 0.96), border-color .2s cubic-bezier(0.46, 0.03, 0.52, 0.96);
}
.game_button.top.selected {
    color: white;
}
.game_button.top:after {
    content: "";
    border-top: 7px solid rgba(255,255,255,.2);
    border-left: 6px solid transparent;
    border-right: 6px solid transparent;
    transition: border-top-color .2s cubic-bezier(0.46, 0.03, 0.52, 0.96);
}
.game_button {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px 20px;
    font-size: 15px;
    font-weight: 400;
    color: rgba(255,255,255,.9);
    background-color: #494b57;
    border-bottom: 1px solid #403d46;
    transition: color .2s cubic-bezier(0.46, 0.03, 0.52, 0.96);
}
.game_button:last-child {
    border-bottom: none;
}
.game_dropdown {
    position: absolute;
    left: 0;
    top: 100%;
    z-index: 1;
    width: 100%;
    transform-origin: top;
    transform: rotateX(90deg);
    transition: transform .2s cubic-bezier(0.46, 0.03, 0.52, 0.96);
}
.game_dropdown.show {
    transform: rotateX(0deg);
}
/* Events */
.game_button:hover {
    color: white;
}
.game_button.top:hover {
    color: white;
    background-color: rgba(20,21,31,.5);
    border-color: rgba(73,75,87,1);
}
.game_button.top:hover:after{
    border-top-color: rgba(255,255,255,.5);
}
/* GAME DROPDOWN End*/

