.related_nav .inputholder {
    width: 293px;
}
.related_nav .inputholder img {
    position: absolute;
    top: 10px;
    left: 10px;
    width: 18px;
}
/* BASIC INPUT */
.inputholder {
    position: relative;
    display: flex;
    align-items: center;
    margin-left: 20px;
    width: 100%;
    border: 1px solid rgba(73,75,87,.5);
    transition: border-color .2s cubic-bezier(0.46, 0.03, 0.52, 0.96);
}
.inputholder input {
    width: 100%;
    color: white;
    /*font-family: "Helvetica Neue";*/
    font-size: 15px;
    padding: 10px;
    padding-left: 37px;
    font-weight: 400;
    background-color: rgba(20,21,31,0);
    transition: background-color .2s cubic-bezier(0.46, 0.03, 0.52, 0.96);
}
::-webkit-input-placeholder {
    color: rgba(255,255,255,.2);
    transition: color .2s cubic-bezier(0.46, 0.03, 0.52, 0.96);
}
.inputholder:hover {
    border-color: rgba(73,75,87,1);
}
.inputholder input:focus {
    background-color: rgba(20,21,31,0.5);
}
