/* Modals */
.modal {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: rgba(34, 36, 49, 0.5);
}
.modal_container {
    width: 235px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    box-shadow: 0 0 41px 8px rgba(45, 48, 63, 0.28);
    border-radius: 4px;
    background-color: #2d303f;
}
.modal_content {
    padding: 20px;
}
.modal_header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 20px
}
.modal_function {
    display: flex;
    align-items: center;
    width: 12px;
    transition: opacity .2s ease-in-out;
}
.modal_function:hover {
    opacity: 0.8;
}
.modal_title {
    font-family: "Helvetica Neue";
    font-size: 15px;
    font-weight: 500;
    color: #ffffff;
}
.modal_title span {
    color: #6a6b74;
}
.modal_input {
    border-radius: 2px;
    border: 2px solid rgba(255,255,255,.1);
    width: 100%;
    height: 36px;
    padding: 0 10px;
    margin-bottom: 4px;
    color: rgba(255,255,255,0.15);
    font-family: "Helvetica Neue";
    font-size: 14px;
    font-weight: 500;
    transition: color .2s ease-in-out, border-color .2s ease-in-out;
}
.modal_input:focus {
    border-color: rgba(255,255,255,0.5);
    color: rgba(255,255,255,0.8);
}
.modal_button {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 33px;
    margin: 4px 0;
    border-radius: 2px;
    background-color: #5a7dd1;
    color: #ffffff;
    font-family: "Helvetica Neue";
    font-size: 15px;
    font-weight: 500;
}
.modal_button:after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0,0,0,0);
    border-radius: 2px;
    transition: background-color .2s ease-in-out;
}
.modal_button:hover:after {
    background-color: rgba(0,0,0,.1);
}
.modal_forgot {
    display: flex;
    margin-top: 15px;
    color: #6a6b74;
    font-family: "Helvetica Neue";
    font-size: 13px;
    font-weight: 500;
    text-decoration: underline;
}
.modal_buttons {
    padding: 20px;
    background-color: #272938;
}
.modal_buttons .modal_button {
    margin: 0;
}
.modal_twitch {
    position: relative;
    display: flex;
    align-items: center;
    height: 33px;
    padding: 0 15px;
    margin-bottom: 12px;
    border-radius: 2px;
    background-color: #6546a4;
    color: #ffffff;
    font-family: "Helvetica Neue";
    font-size: 15px;
    font-weight: 500;
}
.modal_twitch img {
    display: flex;
    align-items: center;
    width: 15px;
    margin-right: 9px;
}
.modal_twitter {
    position: relative;
    display: flex;
    align-items: center;
    height: 33px;
    padding: 0px 15px;
    border-radius: 2px;
    background-color: #2e9de1;
    color: #ffffff;
    font-family: "Helvetica Neue";
    font-size: 15px;
    font-weight: 500;
}
.modal_twitter img {
    display: flex;
    align-items: center;
    width: 17px;
    margin-right: 7px;
}
.modal_twitch:after, .modal_twitter:after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0,0,0,0);
    border-radius: 2px;
    transition: background-color .2s ease-in-out;
}
.modal_twitch:hover:after, .modal_twitter:hover:after {
    background-color: rgba(0,0,0,.1);
}


/* Modal Checkbox */
.modal_checkbox {
    display: flex;
    align-items: flex-start;
    margin-top: 15px;
}
.modal_checkbox_icon {
    width: 20px;
    height: 20px;
    border-radius: 2px;
    border: 2px solid #424553;
    margin-right: 10px;
    flex-shrink: 0;
    transition: background-color .2s ease-in-out;
}
.modal_agree {
    font-family: "Helvetica Neue";
    font-size: 14px;
    font-weight: 500;
    line-height: 22px;
    color: #5d5f68;
    -webkit-user-select: none;    
    -moz-user-select: none;
    -ms-user-select: none;
    -o-user-select: none;
    user-select: none;
}
.modal_terms {
    font-family: "Helvetica Neue";
    font-size: 14px;
    font-weight: 500;
    line-height: 22px;
    color: #a4a4a7;
}
input[type="checkbox"] {
        position: absolute;
        visibility: hidden;
}
input[type=checkbox]:checked ~ .modal_checkbox_content .modal_checkbox_icon {
    background-color: rgba(0,0,0,0.15);
}

/* Modal Radio */
.modal_radio {
    margin-bottom: 15px;
}
.modal_radio:last-child {
    margin-bottom: 0;
}
.modal_radio_content {
    display: flex;
    align-items: center;
}
.modal_radio_icon {
    display: flex;
    align-items: center;
    height: 22px;
    width: 22px;
    margin-right: 12px;
    border-radius: 50%;
    border: 2px solid rgba(255, 255, 255, 0.15);
    background-color: rgba(0,0,0,0);
    transition: background-color .2s ease-in-out;
    cursor: pointer;
}
.modal_radio_text {
    color: #75778c;
    font-family: "Helvetica Neue";
    font-size: 15px;
    font-weight: 500;
    cursor: pointer;
    -webkit-user-select: none;    
    -moz-user-select: none;
    -ms-user-select: none;
    -o-user-select: none;
    user-select: none;
}
input[type="radio"] {
        position: absolute;
        visibility: hidden;
}
input[type=radio]:checked ~ .modal_radio_content .modal_radio_icon {
    background-color: rgba(0,0,0,0.15);
}


