/* Map Spots */
.map_spot {
    position: absolute;
    width: 18px;
    height: 18px;
    border-radius: 50%;
    background-color: rgba(255, 255, 255, 0.6);
    transform: translate(-50%, -50%) scale(0.8);
    transition: background-color .2s ease-in-out, transform .2s ease-in-out;
    cursor: pointer;
}
.map_spot:before {
    content: '';
    position: absolute;
    top: 50%;
    left: 50%;
    height: 24px;
    width: 24px;
    border: 2px solid rgba(255, 255, 255, 0);
    border-radius: 50%;
    transform: translate(-50%, -50%);
    transition: border-color .2s ease-in-out;
}
.map_spot:hover {
    background-color: #ffffff;
    transform: translate(-50%, -50%) scale(1);
}
.map_spot:hover:before {
    border-color: #ffffff;
}
.map_spot.selected {
    background-color: #f18a50;
    transform: translate(-50%, -50%) scale(1);
}
.map_spot.selected:before {
    border-color: #f18a50;
}
/* Map Spots Positioning */
.map_spot:nth-child(1) {
    top: 25%;
    left: 25%;
}
.map_spot:nth-child(2) {
    top: 25%;
    left: 75%;
}
.map_spot:nth-child(3) {
    top: 75%;
    left: 75%;
}
